<template>
  <div class="our">
    <mt-field label="姓名" placeholder="姓名" v-model="username"></mt-field>
    <mt-field label="手机号" placeholder="手机号" type="tel" v-model="phone"></mt-field>
    <mt-field label="QQ" placeholder="请输入QQ号" type="number" v-model="qq"></mt-field>
    <mt-radio
      title="请选择试听课程"
      v-model="course"
      :options="['英语', '法语', '日语']">
    </mt-radio>
    <mt-button @click='sub' type="danger">申请试听</mt-button>
  </div>
</template>
<script>
  import { MessageBox } from 'mint-ui';
  export default{
    data(){
      return{
        username:'',
        phone:'',
        qq:'',
        course:'',
        arry:[],//存数据
      }
    },
    methods:{
      sub(){
        if(this.username&&this.phone&&this.qq&&this.course){
          //给数组加东西
          this.arry.unshift({
            username:this.username,
            phone:this.phone,
            qq:this.qq,
            course:this.course,})
          MessageBox.alert('申请成功').then(action => {
               this.$router.push('/Home')
          });
        }else{
          MessageBox.alert('请输入正确信息')
        }

      }
    }
  }
  import '../assets/style.css'
</script>
<style>
</style>



