import Vue from 'vue'
import Router from 'vue-router'
import Home from '../pages/home'
import Job from '../pages/job'
import About from '../pages/about'
import Our from '../pages/our'
import Teacher from '../pages/teacher'
import Course from '../pages/course'
import FormSub from '../pages/Form'
import News from '../pages/news'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component:Home
    },
    {
      path: '/home',
      component:Home
    },
    {
      path: '/job',
      component:Job
    },
    {
      path: '/about',
      component:About
    },
    {
      path: '/teacher',
      component:Teacher
    },{
      path: '/our',
      component:Our
    },
    {
      path: '/course',
      component:Course
    },
    {
      path: '/Form',
      component:FormSub
    },
    {
      name:'News',
      path: '/News',
      component:News
    },
  ]
})



