<template>
  <div class="foot-nav">
    <router-link :to="{path:'/Form'}">
      <span class="bot"><img src="../assets/images_home/tab_iocn_62.png">申请试听 </span>
    </router-link>
    <span @click="tel"><img src="../assets/images_home/tab_iocn_64.png">电话咨询 </span>
    <span><img src="../assets/images_home/tab_iocn_66.png">在线咨询 </span>
  </div>
</template>
<script>
  import { MessageBox } from 'mint-ui';
  export default{
    data(){
      return{
          num:'18434281312'
      }
    },
    methods:{
      tel(){
        MessageBox({
          title:'',
          message:this.num,
          confirmButtonText:'立即拨号'
        }).then(action => {
         window.location.href ='tel:'+this.num
      });
      }
    }
  }
  import '../assets/foot.css'
</script>
<style>
  .mint-msgbox-message{
    color: #333333;
    font-size: 33px;
  }
  .mint-msgbox-confirm{
    color: #FFFFFF;
    background: #42b983;
    font-size: 22px;
    border-radius: 3px;
  }
  .mint-msgbox-content{
    border: none;
    padding: 50px 20px 15px;
  }

  .mint-msgbox-btns{
    height: 49px;
    line-height: 49px;
    margin: 20px 20px 30px;

  }
span.bot{
  color: #ffffff;
}

</style>



